.contact-banner{
     background-image:linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.3)), url("https://img.pngio.com/diwali-vectors-photos-and-psd-files-free-download-diwali-banner-png-626_223.jpg");
     height: 50vh;  
     justify-content: center;
     display: flex;
     align-items: center;
     color: white;
     position: relative;
     background-size: cover;
     font-family: 'Encode Sans', sans-serif;
}
.contact-map{
     border: none;
     transition: 2s ease-in-out;
}
.contact-img{
     position: absolute;
     left: 90px;
     top:100px;
}
.contact-img img{
     width: 400px;
     height: 300px;
}
#contact{
     height: 80vh;
     display: flex;
     justify-content: center;
     align-items: center;
     font-family: 'Encode Sans', sans-serif;
}

 .form-card{
    
      opacity: 0.3px;
      width: 40%;
      box-shadow: 2px -10px 20px black;
      border-radius: 10px;
      font-weight: 800;  

 }
 .card-title{
      margin-top: 50px;
      padding: 10px;
   
 }
 @media screen and (max-width: 600px) {
     .form-card{
          width: 90%;
     }
     .contact-img{
          position: absolute;
          left: 5px;
          top:170px;
     }
     .contact-img img{
          width: 200px;
          height: 200px;
     }
     }

     label{
          padding: 5px;
          text-align: center;
     }

.card-button{
     margin-bottom: 20px;
}