@import url('https://fonts.googleapis.com/css2?family=Carter+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');

.hero-image {
     background-image: linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.3)), url("https://k6u8v6y8.stackpathcdn.com/blog/wp-content/uploads/2014/07/Religious-Places-to-visit-in-India.jpg");
     height: 90vh;
     background-position: center;
     background-repeat: no-repeat;
     background-size: cover;
     position: relative;
   }
   
   .hero-text {
     text-align: center;
     position: absolute;
     top: 50%;
     left: 50%;
     transform: translate(-50%, -50%);
     color: white;
     font-family: 'Pacifico', cursive;
   }
   
   .hero-text button {
     border: none;
     outline: 0;
     display: inline-block;
     padding: 10px 25px;
     color: black;
     background-color: #ddd;
     text-align: center;
     cursor: pointer;
   }
.w-40{
  width: 300px;
  height: 400px;
  margin: auto;
}
footer{
  color: white;
  text-align: center;
  background-color: black;
  font-family: 'Encode Sans', sans-serif;
  text-align: left !important;
}

.footer-top{
  padding-top:40px;
}

.footer-top hr{
  width: 50px;
  height: 2px;
  background-color:#FF7550;
  margin: 0;
  margin-top: 15px;
  margin-bottom: 20px;
  cursor: pointer;
}
.footer-top hr:hover{
  transform: rotate(40deg);
  height: 2px;
  background-color:#FF7550;
  margin: 0;
  margin-top: 15px;
  margin-bottom: 20px;
}
hr.hr{
  width: 80px;
  height: 1px;
  background-color:#FF7550;
  margin: 0;
  margin-top: 5px;
  margin-bottom: 20px;
  cursor: pointer;
}
hr.hr:hover{
  transition: 2s all  ;
  width: 50px;
  
  height: 1px;
  background-color:#FF7550;
  margin: 0;
  margin-top: 15px;
  margin-bottom: 20px;

}

.footer-top p{
  font-size: 16px !important;
}

.footer-img img{
  width: 80px ;
  height: 80px;
}
.footer-b{
  padding-bottom: 10px;
}

.dubai a{
  font-size: 16px;
}

.footer-top a{
  text-align: left !important;
  text-decoration: none;
  color: white;
}
img.img-temp{
  height: 150px;
}
@media screen and (max-width: 600px) {
  .col-md-3.m-d-none {
    display: none !important;
  }
 
 }

 @media screen and (max-width:600px) {
  .footer-image img{
    position: absolute;
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100px !important;
    display: none;
  }
  
}
 

