}
.blog-banner{
     height: 50vh;
     background-image:linear-gradient(rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.3)), url("https://img.pngio.com/diwali-vectors-photos-and-psd-files-free-download-diwali-banner-png-626_223.jpg");
