h1{
     font-family: 'Encode Sans', sans-serif;
}
p{
     font-family: 'Encode Sans', sans-serif;
}
.booking-logo{
     width: 180px;
     height: 100px;
}

#booking{
     display: flex;
     justify-content: center;
     align-items: center;
}


.booking-card{
     width: 400px;

     border-radius: 20px;
     box-shadow: 10px 10px 20px black ;
}
.booking-bg{
     background-color:black;
     padding: 10px;
     border-radius: 0% 90% 90% 0%;
}
.booking-bg1{
     background-color:orange;
}
.booking-bg label{
     color:#c96;
}
input{
     transition: 2s;
}

input:hover{
     transform: scale(1.1);
}
.booking-bg h1{
     color:#c96;
}
.booking-bg button{
     color:black;
     background-color: #c96 !important;
}
.booking-bg button:hover{
     background-color: black !important;
     color:white;
}

.booking-card h1{
     text-align: center;  
}

.cards{

     height: 100vh;
     margin: 0 auto;
}

.card-width{
     float: left;
     margin: 10px;
     padding: 5px;
   
}
.card-content{   
     padding: 20px;
     text-align: center;
     font-family: 'Encode Sans', sans-serif;
}


img{
     width: 100%;
     height: 300px;
     vertical-align: middle;
     padding: 0;
     
}
@media screen and (min-width: 700px) {
    
     .card-width{
          width: 250px;
          vertical-align: middle;
          padding: 5px;
          cursor: pointer;
          
     }
     .card-width:hover{
          border-radius: 5px;
          box-shadow: 5px 5px 20px black;
        
     }
    }


#dummy{
     height: 50vh;
     background-image:linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 1)),url("https://demo.wenthemes.com/travel-gem/wp-content/uploads/sites/65/2018/10/banner-6.jpg");
     background-size: 100%;
     background-position: cover;
}

.dummy-content{
     width: 60%;
     margin: 0 auto;
     font-size: 20px;
     text-align: center;
}
.dummy-content p{
     margin-top: 50px;
}

.img-banner{
     margin-top: 50px;
     height: 200px !important;
}
@media screen and (max-width: 600px) {
     .img-banner{
          display: none;
     }
    }