*{
     font-family: 'Encode Sans', sans-serif;
}
.card-wrapper{
     padding: 10px;
     font-family: 'Encode Sans', sans-serif;     

}
.details{
     padding: 20px;
     text-align: center;
     color: antiquewhite;
}

.card-img img{
     height: 300px;
     width:100%;
     border-radius: 10%;    
}

.card-img{
     transition: transform .3s;
     cursor: pointer;
}
.card-img:hover{
     transform: scale(1.05)
}