*{
     font-family: 'Encode Sans', sans-serif;
    
}
.banner{
     height: 50vh;
     background-image:linear-gradient(rgba(0, 0, 0,1), rgba(0, 0, 0, 0.2)), url("https://img.pngio.com/diwali-vectors-photos-and-psd-files-free-download-diwali-banner-png-626_223.jpg");
     width: 100%;
     background-size: cover;
     position: relative;
     display: flex;
     justify-content: center;
     align-items: center; 
     text-align: center; 
     font-family: 'Encode Sans', sans-serif;
}
img.img-banner{
     width: 300px;
     margin-top: 10px;
}
.banner h1{
     color: white;
     font-weight: 900 !important;
}
.text-gold{
     font-family: 'Encode Sans', sans-serif !important;

     color:#c96 !important;
}

@media screen and (min-width: 600px) {
     
     #team{
          position: relative;
          background-color: black;
          height: 100vh;
          box-shadow: 10px 10px 20px white;
          margin-bottom: 50px;
     }
     #team h1{
          color: #c96;
     }
     #team h5{
          color: #c96;
     }
    }

   
.blog img{
     height: 400px;
     border-radius: 10px;
     position: relative;
}

.blog .card{
     height: 400px;
     border-radius: 10px;
}

@media screen and (max-width: 600px) {
     .mb-card{
          margin-bottom: 20px;
     }
    }